/**
 * Convert base64 encoded data into the Blob object
 * 
 * @param {string} b64Data Base 64 encoded data or data URL
 * @param {string} contentType 
 * @param {number} [sliceSize=1048576]
 * @returns Promise
 */
var defaultSliceSize = 1048576;

var atobStrict = false;

try {
    void atob(' ');
} catch (e) {
    atobStrict = true;
}

function b64toBlob(b64Data, contentType, sliceSize) {
    var s = b64Data,
        type = '',
        start = 0;
    if (b64Data.substr(0, 5) === 'data:') {
        start = 5;
        while (b64Data.charAt(start++) !== ',') {
            if (b64Data.charAt(start) !== ';') {
                if (!type) {
                    type = b64Data.substring(5, start);
                }
            }
        }
        s = b64Data.substring(start);
        if (b64Data.substring(5, start).indexOf(';base64') === -1) {
            return Promise.resolve(new Blob([s], {
                type: contentType || type
            }));
        }
    }
    return new Promise(function (resolve, reject) {
        function callback(result) {
            resolve(new Blob(result, {
                type: contentType || type
            }));
        }
        function workerCode() {
            var self = this;
            self.onmessage = function (e) {
                var byteCharacters = e.data[0],
                    sliceSize = e.data[1],
                    byteArrays = [];
                for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
                    var slice = byteCharacters.slice(offset, offset + sliceSize),
                        byteNumbers = new Array(slice.length);
                    for (var i = 0; i < slice.length; i++) {
                        byteNumbers[i] = slice.charCodeAt(i);
                    }
                    byteArrays.push(new Uint8Array(byteNumbers));
                }
                self.postMessage(byteArrays);
            };
        }

        var param;
                
        try {
            param = [atob(atobStrict ? s.replace(/\s/g, '') : s), sliceSize || defaultSliceSize];
        } catch (e) {
            reject(e);
            return;
        }
    
        try {
            var code = ('(' + workerCode + ')();').replace('"use strict";', ''),
                src = URL.createObjectURL(new Blob([code], {
                    type: 'application/javascript'
                })),
                worker = new Worker(src);
            worker.onmessage = function (e) {
                URL.revokeObjectURL(src);
                callback(e.data);
            };
            worker.postMessage(param);
        } catch (e) {
            try {
                var self = {
                    postMessage: callback
                };
                workerCode.call(self);
                self.onmessage({
                    data: param
                });
            } catch (e) {
                reject(e);
            }
        }
    });
}

module.exports = b64toBlob;