var Cortona3DSolo = require('solo');
var app = require('app').app;
var expand = require('lib/util').expand;

require('drawing.css');
var SVGDrawing = require('drawing');

var usedModules = require('app').usedModules;

var m_isSVGVisible = false;
var m_urlDrawing = '';

var m_hotspotHighlightColor = '#FF0000';
var m_hotspotSelectionColor = '#FF0000';

var m_hoveredHotspot;

var css = Cortona3DSolo.app.ui.css();

// SVGDrawing hooks
expand(SVGDrawing, {
    delegate: {
        onProgress: function (loaded, total) {
            Cortona3DSolo.dispatch('app.onProgress', loaded, total);
        },
        didClickHotspot: function (name, keys) {
            Cortona3DSolo.dispatch('app.drawing.didSelectHotspot', name, keys);
        },
        didEnterHotspot: function (name) {
            SVGDrawing.toggleHotspotClass(name, 'hover', true);
            Cortona3DSolo.dispatch('app.drawing.didEnterHotspot', name);
            Cortona3DSolo.dispatch('app.drawing.didHoverHotspot', name, true);
        },
        didLeaveHotspot: function (name) {
            SVGDrawing.toggleHotspotClass(name, 'hover', false);
            Cortona3DSolo.dispatch('app.drawing.didLeaveHotspot', name);
            Cortona3DSolo.dispatch('app.drawing.didHoverHotspot', name, false);
        },
        didCallContextMenu: function (name, offsetX, offsetY, target) {
            Cortona3DSolo.dispatch('app.drawing.didCallContextMenu', name, offsetX, offsetY, target);
        },
        onZoomChange: function (zoom) {
            Cortona3DSolo.dispatch('app.drawing.didChangeScale', zoom);
        }
    }
});

/**
 * @private
 */
function attachSVGViewer(canvas) {
    if (!SVGDrawing.svg) {
        var svg = usedModules.drawing.config.element || usedModules.drawing.config.svg || (function () {
            var svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
            var beforeElement = canvas,
                parentElement;
            if (!canvas) {
                require('core.css');
                beforeElement = document.body.firstChild;
                parentElement = document.body;
            } else {
                parentElement = canvas.parentNode;
            }
            parentElement.insertBefore(svg, beforeElement);
            return svg;
        })();

        svg.parentNode.style.overflow = 'hidden';

        SVGDrawing.initialize(svg);
    }
}

function applyStyle() {
    css.render({
        '.cortona3dsolo-svg svg a[name]': {
            '& .hover': {
                stroke: m_hotspotHighlightColor,
                strokeOpacity: 1
            },
            '& path.hover': {
                strokeWidth: '2px',
            },
            '& text.hover': {
                strokeWidth: '.1em',
            },
            '& path.selected': {
                fill: m_hotspotSelectionColor,
                fillOpacity: 0.8
            }
        }
    });
}

var getDocumentBaseURL = require('./util').getDocumentBaseURL;

var appExports = {
    /**
     * The namespace represents an application interface to manage interactive 2D graphics.
     * 
     * Requires the use of the internal module `"drawing"`.
     * 
     * @namespace
     * @memberof module:solo.app
     * @example
Cortona3DSolo.use('drawing', {
    src: 'sample.svg'
});
Cortona3DSolo.app.initialize();
     */
    drawing: {
        /**
         * Loads the 2D illustration file.
         * Supported formats are SVG, JPG, PNG, GIF.
         * 
         * @async
         * @fires "app.drawing.didStartLoadDrawing"
         * @fires "app.drawing.didFinishLoadDrawing"
         * @fires "app.drawing.didFailLoadDrawing"
         * @param {string} imageURL
         * @returns {Promise} A promise 
         * @see external:Promise
         */
        load: function (imageURL) {
            Cortona3DSolo.dispatch('app.drawing.didStartLoadDrawing', imageURL);
            if (typeof app.drawing.willStartLoadDrawing === 'function') {
                var shallBeURL = app.drawing.willStartLoadDrawing.call(app, imageURL);
                if (typeof shallBeURL == 'string') imageURL = shallBeURL;
            }
            imageURL = imageURL.replace(/\.(cgm|tiff?)$/i, '.svg');
            if (imageURL == m_urlDrawing) {
                Cortona3DSolo.dispatch('app.drawing.didFinishLoadDrawing', imageURL);
                return Promise.resolve();
            }
            var url;
            if (app.util && app.util.createResourceURL) url = app.util.createResourceURL(imageURL);
            if (!url) {
                url = imageURL;
                if (!/^[^\/]+:/.test(url)) {
                    url = getDocumentBaseURL() + url;
                }
            }
            return new Promise(function (resolve, reject) {
                SVGDrawing.delegate.didCompleteLoad = function (url) {
                    if (app.util && app.util.revokeResourceURL) app.util.revokeResourceURL(url);
                    m_urlDrawing = imageURL;
                    m_hoveredHotspot = null;
                    Cortona3DSolo.dispatch('app.drawing.didFinishLoadDrawing', imageURL);
                    if (!usedModules.core) {
                        var arg = {
                            type: 'drawing',
                            baseURL: require('./util').getBaseURL(imageURL)
                        };
                        Cortona3DSolo.dispatch('app.didFinishLoadDocument', arg);
                    }
                    resolve();
                };
                SVGDrawing.delegate.failedLoadingDocument = function (url, status) {
                    if (app.util && app.util.revokeResourceURL) app.util.revokeResourceURL(url);
                    m_urlDrawing = imageURL;
                    m_hoveredHotspot = null;
                    SVGDrawing.unload();
                    Cortona3DSolo.dispatch('app.drawing.didFailLoadDrawing', imageURL, status);
                    if (!usedModules.core) {
                        Cortona3DSolo.dispatch('app.didFailLoadDocument', status);
                    }
                    reject(new Error('Image loading failed: status ' + status));
                };
                SVGDrawing.load(url, /\.(png|jpe?g|jpe|gif)$/i.test(imageURL));
            });
        },

        /**
         * Show or hides the 2D window.
         * 
         * @param {boolean} [visible=true]
         */
        show: function (visible) {
            visible = (typeof visible == 'undefined') ? true : !!visible;
            SVGDrawing[visible ? 'show' : 'hide']();
            m_isSVGVisible = !!visible;
        },

        /**
         * Checks if the 2D window visible
         * 
         * @returns {boolean}
         */
        isVisible: function () {
            return m_isSVGVisible;
        },

        /**
         * Selects the hotspot by its name.
         * 
         * @param {string} name
         * @param {HexColorString} [color] A string representing the color. Specify empty string to clear the selection. If omitted, the value specified in the options statement is used. See {@link module:solo.use use()} properties
         */
        selectHotspot: function (name, color) {
            color = typeof color === 'undefined' ? m_hotspotSelectionColor : color;
            if (color === '') {
                SVGDrawing.unselect(name);
            } else {
                SVGDrawing.select(name, color);
            }
        },

        /**
         * Highlights the hotspot by its name.
         * 
         * @param {string} name
         * @param {HexColorString} [color] A string representing the color. Specify empty string to clear the selection. If omitted, the value specified in the options statement is used. See {@link module:solo.use use()} properties
         */
        highlightHotspot: function (name, color) {
            color = typeof color === 'undefined' ? m_hotspotHighlightColor : color;
            if (color === '') {
                SVGDrawing.unhighlight(name);
            } else {
                SVGDrawing.highlight(name, color);
            }
        },

        /**
         * Highlights the hotspot by its name using the color specified in the options statement. See {@link module:solo.use use()} properties.
         * Unlike of the method {@link module:solo.app.drawing.highlightHotspot app.drawing.highlightHotspot()}, this method clears the last selection.
         * 
         * @param {string} name
         */
        hoverHotspot: function (name) {
            if (m_hoveredHotspot) {
                SVGDrawing.toggleHotspotClass(m_hoveredHotspot, 'hover', false);
            }
            if (name) {
                SVGDrawing.toggleHotspotClass(name, 'hover', true);
            }
            m_hoveredHotspot = name;
        },

        /**
         * Toggle the class value for all `SVGPathElement` and `SVGTextElement` inside a hotspot node; i.e., if the class exists then remove it and return `false`, if not, then add it and return `true`.
         * When the `force` argument is present: If the `force` argument evaluates to `true`, add the specified class value, and if it evaluates to `false`, remove it.
         * 
         * @param {string} name
         * @param {string} className
         * @param {boolean} [force]
         * @returns {boolean}
         */
        toggleHotspotClass: function (name, className, force) {
            return SVGDrawing.toggleHotspotClass(name, className, force);
        },

        /**
         * Restores the default view.
         * @since 1.4.3
         */
        reset: function () {
            SVGDrawing.reset();
        },

        /**
         * Change the scale to the specified value.
         * @param {number} value 
         * @since 1.4.3
         */
        scaleBy: function (value) {
            SVGDrawing.scaleBy(value);
        },

        /**
         * Shifts the image along the X and Y axes by the number of pixels specified in the arguments.
         * @param {number} valueX 
         * @param {number} valueY 
         * @since 1.7.0
         */
        shiftBy: function (valueX, valueY) {
            SVGDrawing.shiftBy(valueX, valueY);
        },

        /**
         * Checks if hotspot with specified name exists in the 2D illustration.
         * 
         * @param {string} name
         * @since 1.5.0
         */
        hotspotExists: function (name) {
            return Array.prototype.slice.call(SVGDrawing.svg.getElementsByTagName('a')).some(function (n) {
                return (n.getAttribute('name') === name);
            });
        }
    }
};

/**
 * The HTML `<svg>` element.
 * 
 * @static
 * @member {SVGSVGElement} svg
 * @memberof module:solo.app.drawing
 * @since 1.4.3
 */

/**
 * Gets or sets the color of the hovered hotspot in the 2D illustration.
 * 
 * @static
 * @member {HexColorString} hoverColor
 * @memberof module:solo.app.drawing
 * @since 1.5.0
 */

/**
 * Gets or sets the color of the selected hotspot in the 2D illustration.
 * 
 * @static
 * @member {HexColorString} selectionColor
 * @memberof module:solo.app.drawing
 * @since 1.5.0
 */

/**
 * Gets or sets the maximum scaling limit for the drawing.
 * 
 * @static
 * @member {number} maxScale
 * @memberof module:solo.app.drawing
 * @since 1.6.0
 */

/**
 * Gets or sets the minimum scaling limit for the drawing.
 * 
 * @static
 * @member {number} minScale
 * @memberof module:solo.app.drawing
 * @since 1.6.0
 */

Object.defineProperties(appExports.drawing, {
    'svg': {
        get: function () {
            return SVGDrawing.svg;
        },
        enumerable: true
    },
    'hoverColor': {
        get: function () {
            return m_hotspotHighlightColor;
        },
        set: function (color) {
            m_hotspotHighlightColor = color;
            applyStyle();
        },
        enumerable: true
    },
    'selectionColor': {
        get: function () {
            return m_hotspotSelectionColor;
        },
        set: function (color) {
            m_hotspotSelectionColor = color;
            applyStyle();
        },
        enumerable: true
    },
    'maxScale': {
        get: function () {
            return SVGDrawing.maxScale;
        },
        set: function (value) {
            SVGDrawing.maxScale = value;
        },
        enumerable: true
    },
    'minScale': {
        get: function () {
            return SVGDrawing.minScale;
        },
        set: function (value) {
            SVGDrawing.minScale = value;
        },
        enumerable: true
    }
});

module.exports = {
    configure: function (options) {
        options = options || {};
        m_hotspotHighlightColor = options.hotspotHoverColor || options.hotspotHighlightColor || m_hotspotHighlightColor;
        m_hotspotSelectionColor = options.hotspotSelectionColor || m_hotspotSelectionColor;
        SVGDrawing.enableHoverFilling = (options.features || 0) & app.DRAWING_HOTSPOT_HIGHLIGHT_SOLID;
        SVGDrawing.enableDrawingOptimization = !((options.features || 0) & app.DISABLE_DRAWING_INTERACTIVITY_OPTIMIZATION);
        if (options.maxScale) SVGDrawing.maxScale = +options.maxScale;
        if (options.minScale) SVGDrawing.minScale = +options.minScale;
        applyStyle();
        return appExports;
    },

    attach: attachSVGViewer,

    getSVGViewer: function () {
        return SVGDrawing;
    }
};

/**
 * The hook method is invoked before loading the 2D illustration file. 
 * Must return the URL to the 2D illustration file that should be loaded.
 * 
 * @function willStartLoadDrawing  
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {string} imageURL Request URL to load 2D illustration
 * @returns {string} Returns URL to load 2D illustration
 */

/**
 * The hook method is invoked before loading the 2D illustration.
 * 
 * @function didStartLoadDrawing
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {string} url Request URL to load 2D illustration
 * @see The corresponding event {@link event:"app.drawing.didStartLoadDrawing" "app.drawing.didStartLoadDrawing"}
 */
/**
 * @event "app.drawing.didStartLoadDrawing"
 * @see The corresponding hook method {@link module:solo.app.drawing.didStartLoadDrawing app.drawing.didStartLoadDrawing()}
 */

/**
 * The hook method is invoked when the 2D illustration is failed to load.
 * 
 * @function didFailLoadDrawing
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {string} url Request URL to load 2D illustration
 * @param {string} status Status code
 * @see The corresponding event {@link event:"app.drawing.didFailLoadDrawing" "app.drawing.didFailLoadDrawing"}
 */
/**
 * @event "app.drawing.didFailLoadDrawing"
 * @see The corresponding hook method {@link module:solo.app.drawing.didFailLoadDrawing app.drawing.didFailLoadDrawing()}
 */

/**
 * The hook method is invoked when the 2D illustration is loaded.
 * 
 * @function didFinishLoadDrawing 
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {string} url Request URL to load 2D illustration
 * @see The corresponding event {@link event:"app.drawing.didFinishLoadDrawing" "app.drawing.didFinishLoadDrawing"}
 */
/**
 * @event "app.drawing.didFinishLoadDrawing"
 * @see The corresponding hook method {@link module:solo.app.drawing.didFinishLoadDrawing app.drawing.didFinishLoadDrawing()}
 */

/**
 * The hook is invoked when the hotspot is selected.
 * 
 * @function didSelectHotspot 
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {string} name Name of the selected hotspot
 * @param {number} keys 
 * @see The corresponding event {@link event:"app.drawing.didSelectHotspot" "app.drawing.didSelectHotspot"}
 */
/**
 * @event "app.drawing.didSelectHotspot"
 * @see The corresponding hook method {@link module:solo.app.drawing.didSelectHotspot app.drawing.didSelectHotspot()}
 */

/**
 * The hook is invoked when the hotspot is highlighted.
 * 
 * @function didHoverHotspot 
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {string} name Name of the selected hotspot
 * @param {boolean} hover -
 * - true - Hotspot is highlighted
 * - false - Hotspot is not highlighted
 * @see The corresponding event {@link event:"app.drawing.didHoverHotspot" "app.drawing.didHoverHotspot"}
 */
/**
 * @event "app.drawing.didHoverHotspot"
 * @see The corresponding hook method {@link module:solo.app.drawing.didHoverHotspot app.drawing.didHoverHotspot()}
 */

/**
 * The hook method is invoked when the context menu is called in the 2D window. 
 * The mouse uses secondary button click. The touch device uses long tap.
 * 
 * @function didCallContextMenu 
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {string} name Name of the clicked hotspot or empty string
 * @param {number} offsetX The X coordinate relative to the target element 
 * @param {number} offsetY The Y coordinate relative to the target element
 * @param {HTMLElement} target Target element (parent of the element `<svg>`)
 * @see The corresponding event {@link event:"app.drawing.didCallContextMenu" "app.drawing.didCallContextMenu"}
 */
/**
 * @event "app.drawing.didCallContextMenu"
 * @see The corresponding hook method {@link module:solo.app.drawing.didCallContextMenu app.drawing.didCallContextMenu()}
 */

/**
 * The hook method is invoked when the scale of 2D illustration is changed.
 * 
 * @function didChangeScale 
 * @abstract 
 * @memberof module:solo.app.drawing
 * @param {number} zoom The value of the scale
 * @see The corresponding event {@link event:"app.drawing.didChangeScale" "app.drawing.didChangeScale"}
 */
/**
 * @event "app.drawing.didChangeScale"
 * @see The corresponding hook method {@link module:solo.app.drawing.didChangeScale app.drawing.didChangeScale()}
 */