        var png = canvas.toDataURL("image/png");
        var image = document.createElementNS(SVGNS, 'image');

        image.setAttribute('image-rendering', 'optimizeQuality');
        image.setAttribute('width', img.width);
        image.setAttribute('height', img.height);
        m_svgShadow.setAttribute('viewBox', '0 0 ' + img.width + ' ' + img.height);
        image.setAttributeNS(XLINKNS, 'href', png);

        m_viewportShadow = document.createElementNS(SVGNS, 'g');
        var v = m_viewportShadow.transform.baseVal;
        v.initialize(m_svgShadow.createSVGTransform());
        v.appendItem(m_svgShadow.createSVGTransform());
        m_viewportShadow.appendChild(image);
        m_svgShadow.appendChild(m_viewportShadow);
