    Array.prototype.slice.call(m_svgElement.getElementsByTagName('a')).forEach(function (n) {
        if (n.getAttribute('name') === hotspotName) {
            getHotspotNodes(n).forEach(function (path) {
                if (m_enableHoverFilling) {
                    path.style.stroke = '';
                    path.style.strokeWidth = '';
                    path.style.strokeOpacity = '';
                }
                if (path.tagName !== 'text') {
                    path.style.fill = '';
                    path.style.fillOpacity = '';
                }
            });
        }
    });
