    svgElement.style.touchAction = 'none';
    svgElement.style.width = '100%';
    svgElement.style.height = '100%';
    svgElement.style.position = 'absolute';
    svgElement.style.display = 'block';
    svgElement.style.overflow = 'hidden';
    m_svgElement = svgElement;
    m_view = document.createElement('div');
    m_view.classList.add('cortona3dsolo-svg');
    svgElement.parentNode.replaceChild(m_view, svgElement);
    m_view.appendChild(svgElement);
    m_svgShadow = m_view.appendChild(document.createElementNS(SVGNS, 'svg'));
    m_svgShadow.setAttribute('touch-action', 'none');
    m_svgShadow.style.touchAction = 'none';
    m_svgShadow.style.pointerEvents = 'none';
